import fetch from 'node-fetch';
import {useEffect, useState} from 'react';
import { ScrapeProvider } from './scrapeContext';

//custom hook!
function useScrapes(){
  const [scrapes,setScrapes]=useState([{instagramId:''}])
  const [remainingScrapes, setRemainingScrapes] = useState(0)

  // Use effect accept only a function as parameter
  useEffect(function() {
    (async () => {
      console.log('Mounting or Updating')
      const res = await fetch('http://localhost:3000/api/top100')
      const nbScraps = await fetch('http://localhost:3000/api/top100')
      const data = await res.json()
      setScrapes(data)
      setRemainingScrapes(nbScraps)
      console.log(nbScraps)
    })()
  },[])
  return scrapes
}

const Page = ({ children }) => {
  const scrapes = useScrapes();
  return (
    <ScrapeProvider value={
      {scrapes}
    }>
      <div className="page">
        {children}
      </div>
    </ScrapeProvider>
  );
}

export default Page;
